import React, { useState, useCallback } from 'react'

import { graphql } from 'gatsby'
import debounce from 'lodash.debounce'
import { GatsbyImage } from 'gatsby-plugin-image'
// import { isMobile } from 'react-device-detect'
// import { motion } from 'framer-motion'

import Layout from '@components/common/layout'
import FullScreenHeader from '@components/scrollytelling/annual-report/full-screen-header'
import FullHeightSlide from '@components/scrollytelling/reports/full-height-slide'
import TopNav from '@components/scrollytelling/reports/top-navigation'

// Import data
import CHAPTERS from '@data/annual-report-2021/CHAPTERS'

const AnnualReport2021Story = (props) => {
	const [currentChapter, setCurrentChapter] = useState('welcome')

	const Images = props.data.allFile.edges

	const changeChapter = (newChapter) => {
		setCurrentChapter(newChapter)
	}

	const debouncedchangeChapter = useCallback(debounce(changeChapter, 50), [])

	const getImageByName = useCallback(
		(name) => {
			return Images.filter((el) => el.node.name === name)[0].node.childImageSharp.gatsbyImageData
		},
		[Images]
	)

	return (
		<Layout
			bgColor={'bg-white'}
			shareProps={{
				title: 'The New Humanitarian | Annual Report 2021', // TODO
				url: 'https://interactive.thenewhumanitarian.org/reports/2022/05/31/annual-report-2021', // TODO
				socialTitle: 'The New Humanitarian | Annual Report 2021', // TODO
				socialDescription: 'Our journalism, impact, audience, and more.', // TODO
				socialImage: 'https://interactive.thenewhumanitarian.org/images/reports/annual-report-2021/annual-report-2021--teaser.png',
				nodeId: null,
				updatedTime: '2021-06-27T08:00:00+00:00',
				modiefiedTime: '2021-06-27T08:00:00+00:00',
				publishedTime: '2021-06-27T08:00:00+00:00',
			}}
		>
			<FullScreenHeader
				title={['Journalism from the heart of crises']}
				subtitle={'The New Humanitarian Annual Report 2021'}
				image={getImageByName('header')}
				downloads={[
					{ text: 'Annual Report 2021', url: 'https://assets.thenewhumanitarian.org/s3fs-public/tnh_annual_report_2021.pdf' },
					{
						text: 'Executive Summary',
						url: 'https://assets.irinnews.org/s3fs-public/tnh_annual_report_2021_executive_summary.pdf?qtgTLWGij5JIMqFbd2I_fIzhjyYdPK3j',
					},
				]}
			></FullScreenHeader>

			<div id='start' />

			<TopNav chapters={CHAPTERS} currentChapter={currentChapter} />
			{CHAPTERS.map((el) => {
				return (
					<FullHeightSlide
						chapters={CHAPTERS}
						className={`overflow-x-hidden ${el.className}`}
						chapter={el.id}
						inViewHandler={debouncedchangeChapter}
						key={`chapter--${el.id}`}
					>
						{el.backgroundImage && (
							<GatsbyImage
								alt={''}
								image={getImageByName(el.backgroundImage)}
								style={{
									height: '100%',
									width: '100%',
									position: 'absolute',
									top: 0,
									left: 0,
								}}
								imgStyle={{ objectFit: 'cover' }}
								className={'z-0'}
							/>
						)}
						{el.htmlContent ? <div className={'z-0'}>{el.htmlContent}</div> : <h2 className={'text-burgundy bg-white px-5 py-3'}>{el.title}</h2>}
					</FullHeightSlide>
				)
			})}
		</Layout>
	)
}

export default AnnualReport2021Story

export const query = graphql`
	query AnnualReport2021Images {
		allFile(
			filter: { sourceInstanceName: { eq: "images" }, extension: { in: ["jpg", "png", "jpeg"] }, relativePath: { glob: "reports/annual-report-2021/*" } }
		) {
			edges {
				node {
					extension
					dir
					modifiedTime
					name
					relativePath
					childImageSharp {
						gatsbyImageData(
							placeholder: BLURRED
							width: 1920
							# quality: 85
							layout: FULL_WIDTH
						)
					}
				}
			}
		}
	}
`
